.tomato-history-item {
  padding: 8px;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tomato-history-item .text {
  display: flex;
  align-items: center;
  flex: 1 1;
}
.tomato-history-item .text .time {
  flex-shrink: 0;
  color: #a9a9a9;
  margin-right: 8px;
}
.tomato-history-item .text .edit-filed {
  background: #ffffff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 6px 4px;
  flex: 1 1;
}
.tomato-history-item .text .edit-filed input {
  background: inherit;
  width: 100%;
  border: none;
  outline: none;
}
.tomato-history-item .text .description {
  color: #555;
}
.tomato-history-item .action {
  flex-shrink: 0;
  display: none;
}
.tomato-history-item .action span {
  color: #c9485b;
  margin-left: 4px;
}
.tomato-history-item:hover,
.tomato-history-item.editing {
  background: #f5f5f5;
}
.tomato-history-item:hover .action,
.tomato-history-item.editing .action {
  display: block;
  cursor: pointer;
}
