#login {
  padding: 30vh 10vw;
}
@media (min-width: 500px) {
  #login {
    max-width: 500px;
    margin: auto;
    padding: 30vh 0;
  }
}
#login > * {
  margin: 10px 0;
}
#login > h1 {
  text-align: center;
}
#login > button {
  font-size: 16px;
  width: 100%;
}
#login input {
  font-size: 16px;
}
