.tomato-list {
  margin-top: 16px;
  border-top: 1px solid #ddd;
}
.tomato-list .daily-tomatoes {
  padding-top: 8px;
}
.tomato-list .daily-tomatoes .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
  margin-top: 8px;
}
.tomato-list .daily-tomatoes .title .date-time {
  color: #a9a9a9;
  font-size: 16px;
}
.tomato-list .daily-tomatoes .title .finished-count {
  color: #a9a9a9;
  font-size: 12px;
}
.tomato-list .daily-tomatoes .details .tomato-item {
  font-size: 14px;
  margin: 4px 0;
}
.tomato-list .daily-tomatoes .details .tomato-item .time-range {
  color: #a9a9a9;
}
.tomato-list .daily-tomatoes .details .tomato-item .description {
  margin-left: 8px;
  font-size: 16px;
}
