.add-modal-item {
  margin: 0 16px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.add-modal-item + .add-modal-item {
  margin-top: 8px;
}
.add-modal-item .add-modal-label {
  flex-shrink: 0;
}
.add-modal-item .date-picker {
  width: 100%;
}
.add-modal-item input {
  width: 100%;
}
@media (min-width: 500px) {
  .add-modal-item {
    flex-direction: row;
    align-items: center;
  }
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane {
  position: relative;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  top: -52px;
  font-size: 20px;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes {
  padding: 8px 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title {
  padding: 8px;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date {
  float: left;
  margin-right: 16px;
  position: relative;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date .date-time {
  font-size: 20px;
  color: #555;
  position: relative;
  display: block;
  top: -4px;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date .week-time {
  font-size: 12px;
  color: #a9a9a9;
  position: absolute;
  right: 2px;
  bottom: -10px;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .finished-count {
  font-size: 14px;
  color: #a9a9a9;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .total-time {
  color: #a9a9a9;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title::after {
  content: '';
  display: block;
  clear: both;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .details {
  flex: 1 1;
}
@media (min-width: 500px) {
  .tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes {
    padding: 16px;
    flex-direction: row;
  }
  .tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title {
    flex-shrink: 0;
    margin-right: 8px;
    padding: 0;
  }
  .tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date {
    float: none;
  }
  .tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date .date-time {
    position: static;
    margin-right: 4px;
    display: inline-block;
  }
  .tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date .week-time {
    position: static;
  }
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .pagination {
  margin-top: 16px;
  text-align: center;
}
