.count-down {
  position: relative;
  text-align: center;
  height: 32px;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 4px;
  overflow: hidden;
  font-size: 16px;
}
.count-down span {
  position: relative;
  z-index: 1;
}
.count-down .progress-bar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background: #f5f5f5;
  z-index: 0;
}
