#home {
  max-width: 1000px;
  padding: 16px;
  margin: auto;
}
#home header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ddd;
}
#home header .logo {
  width: 1.5em;
  margin: 0.2em;
}
#home header .user {
  cursor: pointer;
}
#home > main {
  padding: 16px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (min-width: 500px) {
  #home > main {
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: nowrap;
  }
}
