.tomato-action .start-button {
  width: 100%;
  font-size: 16px;
}
.tomato-action .input-wrapper {
  position: relative;
}
.tomato-action .count-down-wrapper {
  position: relative;
}
.tomato-action .abort {
  font-size: 1.1em;
  position: absolute;
  right: -6px;
  background: #ffffff;
  top: -6px;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.tomato-action .abort:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  color: #c9485b;
}
