.todo-item {
  display: flex;
  align-items: center;
  padding: 8px;
  border-bottom: 1px solid #ddd;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.todo-item:hover {
  background: #f5f5f5;
}
.todo-item:first-child {
  border-top: 1px solid #ddd;
}
.todo-item.editing {
  background: #ffffdd;
}
.todo-item.completed .text {
  text-decoration: line-through;
  color: #a9a9a9;
}
.todo-item .edit-filed,
.todo-item .text {
  padding-left: 8px;
  flex: 1 1;
}
.todo-item .edit-filed {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.todo-item .edit-filed input {
  background: inherit;
  width: 100%;
  border: none;
  outline: none;
}
.todo-item .edit-filed .icon-wrapper {
  flex-shrink: 0;
}
.todo-item .edit-filed .icon-wrapper i {
  margin-left: 4px;
  color: #a9a9a9;
}
.todo-item .text {
  display: flex;
}
