.statistics > ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.statistics > ul li {
  border: 1px solid #ddd;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  cursor: pointer;
  position: relative;
  background: #ffffff;
}
.statistics > ul li:first-child {
  border-radius: 4px 0 0 4px;
}
.statistics > ul li:last-child {
  border-radius: 0 4px 4px 0;
}
.statistics > ul li + li {
  border-left: none;
}
.statistics > ul li.active {
  background: #f5f5f5;
}
.statistics > ul li .text {
  margin: 8px;
}
.statistics > ul li .text p:first-child {
  font-size: 16px;
}
.statistics > ul li .text p:nth-child(2) {
  font-size: 12px;
  color: #a9a9a9;
}
.statistics > ul li .text p:last-child {
  position: absolute;
  right: 8px;
  bottom: 0;
  font-size: 40px;
}
.statistics > ul li .graph {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -40px;
}
.statistics > ul li .graph svg {
  height: 70px;
  max-width: 100%;
  margin-left: -1px;
}
@media (min-width: 500px) {
  .statistics ul li .text p:nth-child(2) {
    font-size: 8px;
  }
  .statistics ul li .text p:last-child {
    position: relative;
    right: 0;
    top: -8px;
  }
  .statistics ul li .graph {
    margin-top: -78px;
  }
  .statistics ul li .graph svg {
    height: 60px;
    width: 239px;
  }
}
.statistics .history-details {
  margin: 0;
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}
@media (min-width: 500px) {
  .statistics .history-details {
    margin: 16px;
  }
}
