#register {
  padding: 30vh 10vw;
}
@media (min-width: 500px) {
  #register {
    max-width: 500px;
    margin: auto;
    padding: 30vh 0;
  }
}
#register > * {
  margin: 10px 0;
}
#register > h1 {
  text-align: center;
}
#register > button {
  font-size: 16px;
  width: 100%;
}
#register input {
  font-size: 16px;
}
