.todo-history-item {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  padding: 8px;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
}
.todo-history-item .text .time {
  color: #a9a9a9;
  margin-right: 8px;
}
.todo-history-item .text .description {
  color: #555;
}
.todo-history-item .action {
  display: none;
}
.todo-history-item .action span {
  color: #c9485b;
}
.todo-history-item .action span:first-child {
  margin-right: 4px;
}
.todo-history-item:hover {
  background: #f5f5f5;
}
.todo-history-item:hover .action {
  display: block;
  cursor: pointer;
}
